/** suggestions */

.autotag-suggestions {
  position: absolute;
  margin-left: 75px;
}

.autotag-suggestions ul {
  margin: 4px -1px;
  padding: 0;
  list-style: none;
  background: white;
  border: 1px solid #d1d1d1;
  border-radius: 2px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
}

.autotag-suggestions li {
  border-bottom: 1px solid #ddd;
  padding: 6px 8px;
}

.autotag-suggestions li mark {
  text-decoration: underline;
  background: none;
  font-weight: 600;
}

.autotag-suggestions li:hover {
  cursor: pointer;
  background: #eee;
}

.autotag-suggestions li.is-active {
  background: #b7cfe0;
}

.autotag-suggestions li.is-disabled {
  opacity: 0.5;
  cursor: auto;
}

